import React from 'react';
/*
* 方块
*/
const Square = (props) => {
    let numClass = ['number'];
    if (props.num > 0) {
        // 更改字体大小
        const fsLen = props.num.toString().length;
        let fsClass = 'fs-';
        if (fsLen <= 3) {
            fsClass += 3;
        }
        else if (fsLen === 4) {
            fsClass += 4;
        }
        else {
            fsClass += 5;
        }
        // 每11个数轮回一次颜色序列
        const colorClass = `color-${Math.log(props.num) / Math.log(2) % 11}`;
        numClass.push('show', fsClass, colorClass);
    }
    return (React.createElement("div", { className: "square" },
        React.createElement("div", { className: numClass.join(' ') }, props.num)));
};
export default Square;
