import React from 'react';
import Square from './Square';
import { MIN_DISTANCE_PC, MIN_DISTANCE_M, RIGHT, LEFT, DOWN, UP } from '../utils/constants';
const Board = (props) => {
    const { squares, onMove } = props;
    let coordinate = {
        X: 0,
        Y: 0
    };
    const SquaresUI = squares.map((n, idx) => {
        return React.createElement(Square, { num: n, key: idx });
    });
    // 获取方向
    function getDirection(curX, curY, minDistance) {
        // 判断方向
        const deltaX = curX - coordinate.X;
        const deltaY = curY - coordinate.Y;
        const absDeltaX = Math.abs(deltaX);
        const absDeltaY = Math.abs(deltaY);
        // 定义鼠标移动不小于100，才认为是要操作，避免过敏误操作界面
        if (absDeltaX < minDistance && absDeltaY < minDistance)
            return null;
        // delta正值，认为是↓ 或 →，否则是↑ 或 ←
        const dirX = deltaX > 0 ? RIGHT : LEFT;
        const dirY = deltaY > 0 ? DOWN : UP;
        const deltaDir = absDeltaX - absDeltaY;
        let direction;
        if (deltaDir > 0) { // 如果deltaX - deltaY > 0 ,认为 往→ 或 ←
            direction = dirX;
        }
        else if (deltaDir < 0) { // 如果deltaX - deltaY < 0 ,认为 往↓ 或 ↑
            direction = dirY;
        }
        else { // 如果deltaX - deltaY = 0, 随机上下 或 左右
            direction = Math.round(Math.random()) ? dirX : dirY;
        }
        // reset
        coordinate.X = 0;
        coordinate.Y = 0;
        return direction;
    }
    // 触摸事件
    function handleTouchStart(e) {
        e.preventDefault();
        e.stopPropagation();
        // console.log('handleTouchStart', e)
        // // TODO: 多于一个手指不做反映
        // if (e.touches.length > 1) return
        coordinate.X = e.touches[0].clientX;
        coordinate.Y = e.touches[0].clientY;
    }
    function handleTouchEnd(e) {
        e.preventDefault();
        e.stopPropagation();
        // console.log('handleTouchEnd', e)
        // TODO: 多于一个手指不做反映
        // if (e.changedTouches.length > 1) return
        // 不在区域内，不执行后续代码
        if (!coordinate.X && !coordinate.Y) {
            // reset
            coordinate.X = 0;
            coordinate.Y = 0;
            return;
        }
        const direction = getDirection(e.changedTouches[0].clientX, e.changedTouches[0].clientY, MIN_DISTANCE_M);
        if (direction) {
            onMove(direction);
        }
    }
    // 鼠标事件
    function handleMouseDown(e) {
        e.preventDefault();
        e.stopPropagation();
        // console.log('handleMouseDown', e, e.clientX, e.clientY)
        coordinate.X = e.clientX;
        coordinate.Y = e.clientY;
    }
    function handleMouseUp(e) {
        e.preventDefault();
        e.stopPropagation();
        // 不在区域内，不执行后续代码
        if (!coordinate.X && !coordinate.Y) {
            // reset
            coordinate.X = 0;
            coordinate.Y = 0;
            return;
        }
        const direction = getDirection(e.clientX, e.clientY, MIN_DISTANCE_PC);
        // console.log('handleMouseUp', e, e.clientX, e.clientY, direction)
        if (direction) {
            onMove(direction);
        }
    }
    // 键盘事件
    function handleKeyUp(e) {
        e.stopPropagation();
        // console.log('handleKeyUp', e)
        switch (e.code) {
            case UP:
            case RIGHT:
            case DOWN:
            case LEFT: {
                const direction = e.code;
                if (direction) {
                    onMove(direction);
                }
                break;
            }
        }
    }
    document.onkeyup = handleKeyUp;
    return (React.createElement("div", { className: "board", onMouseDown: handleMouseDown, onMouseUp: handleMouseUp, onTouchStart: handleTouchStart, onTouchEnd: handleTouchEnd }, SquaresUI));
};
export default Board;
