import React from 'react';
/*
* 排名项
*/
const RankItem = (props) => {
    const positionClass = ['position'];
    switch (props.position) {
        case 1: {
            positionClass.push('first');
            break;
        }
        case 2: {
            positionClass.push('second');
            break;
        }
        case 3: {
            positionClass.push('third');
            break;
        }
    }
    return (React.createElement("li", { className: "rank-item" },
        React.createElement("span", { className: positionClass.join(' ') }, props.position || 0),
        React.createElement("span", { className: "name" }, props.name || '-'),
        React.createElement("span", { className: "score" }, props.score || 0)));
};
export default RankItem;
