import axios from 'axios';
axios.defaults.baseURL = '/query';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
export default axios;
export function formatFormUrlencoded(obj) {
    let res = [];
    for (let k in obj) {
        res.push(`${k}=${obj[k] || ''}`);
    }
    return res.join('&');
}
