import React from 'react';
import '../scss/loading.scss';
const Loading = (props) => {
    return (React.createElement("div", { className: "loading" },
        React.createElement("div", { className: "main" },
            React.createElement("div", { className: "particle particle_1" }, "2"),
            React.createElement("div", { className: "particle particle_2" }, "0"),
            React.createElement("div", { className: "particle particle_3" }, "4"),
            React.createElement("div", { className: "particle particle_4" }, "8"))));
};
export default Loading;
