import React, { useState, useEffect } from 'react';
import Score from '../components/Score';
import Board from '../components/Board';
import GameButton from '../components/GameButton';
import Modal from '../components/Modal';
import ResultModal from '../components/ResultModal';
import { usePrevious } from '../utils/hooks';
import { MATRIX_ROW, MATRIX_COL, RIGHT, LEFT, DOWN, UP } from '../utils/constants';
import '../scss/game.scss';
/**
 * 游戏
 */
const Game = (props) => {
    // 只记录最近操作的2步，故包括初始数组最多长度为3
    const [history, setHistory] = useState([{ squares: new Array(16).fill(0) }]);
    const [scores, setScores] = useState([0]);
    const [isOver, setIsOver] = useState(false);
    const [bestScore, setBestScore] = useState(Number(localStorage.getItem('bestScore') || 0));
    // 最高分原始值
    const preBestScore = usePrevious(bestScore) || 0;
    // btn-undo是否可点击
    const disabledUndo = isOver || history.length < 3;
    // 当前格对应一元数组的下标 —— 函数式编程
    const getSquareIdx = (row, col) => (row - 1) * MATRIX_COL + (col - 1);
    // 获取行号
    const getRow = (col, squareIdx) => (squareIdx - col + 1) / MATRIX_COL + 1;
    // 获取列号
    const getCol = (row, squareIdx) => squareIdx + 1 - (row - 1) * MATRIX_COL;
    // 随机选一为0的square随机设置2或4
    function genNewNum(squares) {
        const arr = squares.slice();
        const emptyIdxs = [];
        for (let i = 0; i < arr.length; i++) {
            if (!arr[i]) {
                emptyIdxs.push(i);
            }
        }
        if (!emptyIdxs.length) { // 返回自身，避免用空数组覆盖原来结果
            return arr;
        }
        // 从1到coutZero里随机生成一个位置
        const idx = emptyIdxs[Math.round(Math.random() * (emptyIdxs.length - 1))];
        // 随机设置2或4
        arr[idx] = Math.round(Math.random()) ? 2 : 4;
        // console.log('Sequares=', arr)
        return arr;
    }
    // 检查垂直于用户移动方向的方向上是否存在合并或者移动的可能性
    function checkPerpendicularDirPossibility(direction, squares) {
        let possibility = false;
        switch (direction) {
            case UP:
            case DOWN: {
                // 垂直的方向为：LEFT
                // 从第1行开始
                for (let row = 1; row <= MATRIX_ROW; row++) {
                    if (possibility)
                        break;
                    // 从第2列开始往移动方向合并
                    for (let col = 2; col <= MATRIX_COL; col++) {
                        if (possibility)
                            break;
                        // 每列开始值对应一元数组的下标
                        const curColIdx = getSquareIdx(row, col);
                        if (squares[curColIdx]) { // 当前列有值的情况
                            // preColIdx ：移动方向的前一列Idx
                            const preColIdx = getSquareIdx(row, col - 1);
                            if (!squares[preColIdx]
                                || squares[preColIdx] && squares[curColIdx] === squares[preColIdx]) {
                                possibility = true;
                            }
                        }
                        else {
                            possibility = true;
                        }
                    }
                }
                break;
            }
            case LEFT:
            case RIGHT: {
                // 垂直的方向为：UP
                // 从第1列开始
                for (let col = 1; col <= MATRIX_COL; col++) {
                    if (possibility)
                        break;
                    // 从第2行开始往移动方向合并
                    for (let row = 2; row <= MATRIX_ROW; row++) {
                        if (possibility)
                            break;
                        // 每行开始值对应一元数组的下标
                        const curRowIdx = getSquareIdx(row, col);
                        if (squares[curRowIdx]) { // 当前行有值的情况
                            // preRowIdx ：移动方向的前一行Idx
                            let preRowIdx = getSquareIdx(row - 1, col);
                            if (!squares[preRowIdx]
                                || squares[preRowIdx] && squares[curRowIdx] === squares[preRowIdx]) {
                                possibility = true;
                            }
                        }
                        else {
                            possibility = true;
                        }
                    }
                }
                break;
            }
        }
        return possibility;
    }
    /**
     * 移动处理
     *
     **/
    function handleMove(direction) {
        if (isOver)
            return;
        const currentHistory = history[history.length - 1];
        const arr = currentHistory.squares.slice();
        let scoreDelta = 0; // 本轮新增的得分
        switch (direction) {
            case UP: {
                if (MATRIX_ROW > 1) {
                    // 从第1列开始
                    for (let col = 1; col <= MATRIX_COL; col++) {
                        // 每行和前一行的下标相差值
                        const rowIdxDelta = MATRIX_COL;
                        // 行的开始指针，根据是否合并而更改移动后能比较的方块的最小下标值，默认从第1列开始
                        let startPointer = 1;
                        // 从第2行开始往移动方向合并
                        for (let row = 2; row <= MATRIX_ROW; row++) {
                            // 最后一个下标序号
                            const preEndIdx = getSquareIdx(startPointer, col);
                            // 每行开始值对应一元数组的下标
                            const curRowIdx = getSquareIdx(row, col);
                            if (arr[curRowIdx]) { // 当前行有值的情况
                                // console.log(`startPointer=${startPointer}, preEndIdx=${preEndIdx}, col=${col}, row=${row}, squareIdx=${curRowIdx}`)
                                // preRowIdx ：移动方向的前一行Idx
                                for (let preRowIdx = getSquareIdx(row - 1, col); preRowIdx >= preEndIdx; preRowIdx -= rowIdxDelta) {
                                    if (arr[preRowIdx]) {
                                        if (arr[curRowIdx] === arr[preRowIdx]) { // 合并
                                            arr[preRowIdx] *= 2;
                                            arr[curRowIdx] = 0;
                                            // 最后+1是因为起始指针从合并的下一位开始
                                            startPointer = getRow(col, preRowIdx) + 1;
                                            // 计算分数
                                            scoreDelta += arr[preRowIdx];
                                        }
                                        else if ((curRowIdx - preRowIdx) / rowIdxDelta > 1) { // curRowIdx - preRowIdx 之间有空格，则放置离lastRowIdx最近一格
                                            arr[preRowIdx + rowIdxDelta] = arr[curRowIdx];
                                            arr[curRowIdx] = 0;
                                        }
                                        break;
                                    }
                                    else if (!arr[preRowIdx] && preRowIdx === preEndIdx) { // 往移动方向进方向，如果到达头部，则放置于此
                                        arr[preRowIdx] = arr[curRowIdx];
                                        arr[curRowIdx] = 0;
                                    }
                                    // 继续往移动方向进方向寻找
                                }
                            }
                        }
                    }
                }
                break;
            }
            case DOWN: {
                // 从倒数第2行开始往移动方向合并
                if (MATRIX_ROW > 1) {
                    // 从第1列开始
                    for (let col = 1; col <= MATRIX_COL; col++) {
                        // 每行和前一行的下标相差值
                        const rowIdxDelta = MATRIX_COL;
                        // 行的开始指针，根据是否合并而更改移动后能比较的方块的最小下标值，默认从第1列开始
                        let startPointer = MATRIX_ROW;
                        // 从倒数第2行开始往移动方向合并
                        for (let row = MATRIX_ROW - 1; row >= 1; row--) {
                            // 最后一个下标序号
                            const preEndIdx = getSquareIdx(startPointer, col);
                            // 每行开始值对应一元数组的下标
                            const curRowIdx = getSquareIdx(row, col);
                            if (arr[curRowIdx]) { // 当前行有值的情况
                                // console.log(`startPointer=${startPointer}, preEndIdx=${preEndIdx}, col=${col}, row=${row}, squareIdx=${curRowIdx}`)
                                // preRowIdx ：移动方向的前一行Idx
                                for (let preRowIdx = getSquareIdx(row + 1, col); preRowIdx <= preEndIdx; preRowIdx += rowIdxDelta) {
                                    if (arr[preRowIdx]) {
                                        if (arr[curRowIdx] === arr[preRowIdx]) { // 合并
                                            arr[preRowIdx] *= 2;
                                            arr[curRowIdx] = 0;
                                            // 最后-1是因为起始指针从合并的下一位开始
                                            startPointer = getRow(col, preRowIdx) - 1;
                                            // 计算分数
                                            scoreDelta += arr[preRowIdx];
                                        }
                                        else if ((preRowIdx - curRowIdx) / rowIdxDelta > 1) { // curRowIdx - preRowIdx 之间有空格，则放置离lastRowIdx最近一格
                                            arr[preRowIdx - rowIdxDelta] = arr[curRowIdx];
                                            arr[curRowIdx] = 0;
                                        }
                                        break;
                                    }
                                    else if (!arr[preRowIdx] && preRowIdx === preEndIdx) { // 往移动方向进方向找，如果到达头部，则放置于此
                                        arr[preRowIdx] = arr[curRowIdx];
                                        arr[curRowIdx] = 0;
                                    }
                                    // 继续往移动方向进方向寻找
                                }
                            }
                        }
                    }
                }
                break;
            }
            case LEFT: {
                // 从第2列开始往移动方向合并
                if (MATRIX_COL > 1) {
                    // 从第1行开始
                    for (let row = 1; row <= MATRIX_ROW; row++) {
                        // 每列和前一列的下标相差值
                        const colIdxDelta = 1;
                        // 列的开始指针，根据是否合并而更改移动后能比较的方块的最小下标值，默认从第1列开始
                        let startPointer = 1;
                        // 从第2列开始往移动方向合并
                        for (let col = 2; col <= MATRIX_COL; col++) {
                            // 最后一个下标序号
                            const preEndIdx = getSquareIdx(row, startPointer);
                            // 每列开始值对应一元数组的下标
                            const curColIdx = getSquareIdx(row, col);
                            if (arr[curColIdx]) { // 当前列有值的情况
                                // console.log(`startPointer=${startPointer}, preEndIdx=${preEndIdx}, col=${col}, row=${row}, squareIdx=${curColIdx}`)
                                // preColIdx ：移动方向的前一列Idx
                                for (let preColIdx = getSquareIdx(row, col - 1); preColIdx >= preEndIdx; preColIdx -= colIdxDelta) {
                                    if (arr[preColIdx]) {
                                        if (arr[curColIdx] === arr[preColIdx]) { // 合并
                                            arr[preColIdx] *= 2;
                                            arr[curColIdx] = 0;
                                            // 最后+1是因为起始指针从合并的下一位开始
                                            startPointer = getCol(row, preColIdx) + 1;
                                            // 计算分数
                                            scoreDelta += arr[preColIdx];
                                        }
                                        else if ((curColIdx - preColIdx) / colIdxDelta > 1) { // curColIdx - preColIdx 之间有空格，则放置离lastRowIdx最近一格
                                            arr[preColIdx + colIdxDelta] = arr[curColIdx];
                                            arr[curColIdx] = 0;
                                        }
                                        break;
                                    }
                                    else if (!arr[preColIdx] && preColIdx === preEndIdx) { // 往移动方向进方向，如果到达头部，则放置于此
                                        arr[preColIdx] = arr[curColIdx];
                                        arr[curColIdx] = 0;
                                    }
                                    // 继续往移动方向进方向寻找
                                }
                            }
                        }
                    }
                }
                break;
            }
            case RIGHT: {
                // 从倒数第2列开始往移动方向合并
                if (MATRIX_COL > 1) {
                    // 从第1行开始
                    for (let row = 1; row <= MATRIX_ROW; row++) {
                        // 每列和前一列的下标相差值
                        const colIdxDelta = 1;
                        // 列的开始指针，根据是否合并而更改移动后能比较的方块的最小下标值，默认从第1列开始
                        let startPointer = MATRIX_COL;
                        // 从倒数第2列开始往移动方向合并
                        for (let col = MATRIX_COL - 1; col >= 1; col--) {
                            // 最后一个下标序号
                            const preEndIdx = getSquareIdx(row, startPointer);
                            // 每列开始值对应一元数组的下标
                            const curColIdx = getSquareIdx(row, col);
                            if (arr[curColIdx]) { // 当前列有值的情况
                                // console.log(`startPointer=${startPointer}, preEndIdx=${preEndIdx}, col=${col}, row=${row}, squareIdx=${curColIdx}`)
                                // preColIdx ：移动方向的前一列Idx
                                for (let preColIdx = getSquareIdx(row, col + 1); preColIdx <= preEndIdx; preColIdx += colIdxDelta) {
                                    if (arr[preColIdx]) {
                                        if (arr[curColIdx] === arr[preColIdx]) { // 合并
                                            arr[preColIdx] *= 2;
                                            arr[curColIdx] = 0;
                                            // 最后-1是因为起始指针从合并的下一位开始
                                            startPointer = getCol(row, preColIdx) - 1;
                                            // 计算分数
                                            scoreDelta += arr[preColIdx];
                                        }
                                        else if ((preColIdx - curColIdx) / colIdxDelta > 1) { // curColIdx - preColIdx 之间有空格，则放置离lastRowIdx最近一格
                                            arr[preColIdx - colIdxDelta] = arr[curColIdx];
                                            arr[curColIdx] = 0;
                                        }
                                        break;
                                    }
                                    else if (!arr[preColIdx] && preColIdx === preEndIdx) { // 往移动方向进方向，如果到达头部，则放置于此
                                        arr[preColIdx] = arr[curColIdx];
                                        arr[curColIdx] = 0;
                                    }
                                    // 继续往移动方向进方向寻找
                                }
                            }
                        }
                    }
                }
                break;
            }
        }
        // console.log(currentHistory.squares, arr)
        // 如果完全相同，则不发生变化
        if (JSON.stringify(currentHistory.squares) === JSON.stringify(arr)) {
            if (!checkPerpendicularDirPossibility(direction, arr)) {
                const curScore = scores[scores.length - 1];
                if (curScore > preBestScore) {
                    setBestScore(curScore);
                    localStorage.setItem('bestScore', curScore + '');
                }
                setIsOver(true);
                // console.log('================ Game Over')
            }
            return;
        }
        setHistory(history.concat([{ squares: arr }]));
        // 计算分数
        if (scoreDelta) {
            setScores(scores => {
                const prevScore = scores[scores.length - 1];
                const nowScore = prevScore + scoreDelta;
                return [prevScore, nowScore];
            });
        }
        /**
         * 逻辑：为避免捕获过时的属性，setSquares需要异步函数获取
         * 优化：为体现先合并后随机顺序的逻辑，避免混淆，使用setTimeout。经人工调试不生硬，90ms比较符合视觉的展示
         **/
        setTimeout(() => setHistory((oldHistory) => {
            // 当前历史的索引
            const curHistoryIdx = oldHistory.length - 1;
            // 新历史集合
            let newHistory = oldHistory.slice();
            // 随机生成方块并替换当前历史
            newHistory.splice(curHistoryIdx, 1, { squares: genNewNum(newHistory[curHistoryIdx].squares) });
            // 只保留初始记录 和 最近2次的操作记录
            if (newHistory.length > 3) {
                newHistory.splice(1, 1);
            }
            // console.log(`History`, newHistory)
            return newHistory;
        }), 90);
    }
    // 开始游戏
    function startGame() {
        setHistory((oldHistory) => {
            // 新历史集合
            let newHistory = oldHistory.slice(0, 1);
            return newHistory.concat([{ squares: genNewNum(history[0].squares) }]);
        });
        setScores([0]);
    }
    // 撤销上一步
    function undoGame() {
        if (history.length > 2) { // 除了初始数组外，存在上一步方可撤销
            setHistory((oldHistory) => oldHistory.slice(0, oldHistory.length - 1));
            setScores(scores => scores.slice(0, 1));
        }
    }
    // 初始化游戏界面
    useEffect(() => {
        setHistory((oldHistory) => {
            return oldHistory.concat([{ squares: genNewNum(history[0].squares) }]);
        });
    }, []);
    // 控制Modal
    const ModalUI = isOver ? (React.createElement(Modal, null,
        React.createElement(ResultModal, { isShow: isOver, score: scores[scores.length - 1], bestScore: preBestScore, onRestart: () => startGame(), onClose: () => setIsOver(false) })))
        : null;
    return (React.createElement("div", { className: "game" },
        React.createElement("header", null,
            React.createElement("div", { className: "logo" }, "2048"),
            React.createElement(Score, { name: "SCORE", num: scores[scores.length - 1] }),
            React.createElement(Score, { name: "YOUR BEST", num: bestScore }),
            React.createElement(GameButton, { name: "NEW", onClick: () => startGame() }),
            React.createElement(GameButton, { name: "UNDO", btnDisabled: disabledUndo, onClick: () => undoGame() })),
        React.createElement("main", null,
            React.createElement("p", { className: "desc" }, "Join the numbers and get to the 2048 tile!"),
            React.createElement(Board, { squares: history[history.length - 1].squares, onMove: (dir) => handleMove(dir) })),
        ModalUI));
};
export default Game;
