import React from 'react';
import RankItem from './RankItem';
import '../scss/RankList.scss';
/*
* 排名列表
*/
const RankList = (props) => {
    const list = props.list;
    const olClass = ['rank-list'];
    if (props.isShort) {
        olClass.push('short');
    }
    return list.length
        ? (React.createElement("ol", { className: olClass.join(' ') }, list.map((obj) => {
            return (React.createElement(RankItem, { key: obj.id, position: obj.rank_num, name: obj.user_name, score: obj.user_score }));
        })))
        : React.createElement("p", { className: "no-data" }, "\u6682\u65E0\u6570\u636E");
};
export default RankList;
