import React from 'react';
const ResultLayout = (props) => {
    // 是否现得分数超越过去最高分 展示不同的结果
    return props.score > props.bestScore
        ? (React.createElement(React.Fragment, null,
            React.createElement("h1", { className: "title exceed" }, "Congratulations"),
            React.createElement("div", { className: "p-score" },
                "You has got a best score: ",
                React.createElement("span", { className: "num cur best" }, props.score || 0))))
        : (React.createElement(React.Fragment, null,
            React.createElement("h1", { className: "title" }, "Game Over"),
            React.createElement("div", { className: "p-score" },
                "Current Score\uFF1A",
                React.createElement("span", { className: "num cur" }, props.score || 0)),
            React.createElement("div", { className: "p-score best" },
                "Best Score\uFF1A",
                React.createElement("span", { className: "num" }, props.bestScore || 0))));
};
export default ResultLayout;
