import React from 'react';
/*
* 分数
*/
const Score = (props) => {
    return (React.createElement("div", { className: "score" },
        React.createElement("span", { className: "key" }, props.name || ''),
        React.createElement("span", null, props.num || 0)));
};
export default Score;
