import React from 'react';
/*
* 游戏按钮
*/
const GameButton = (props) => {
    const classStr = `btn btn-${props.name.toLowerCase()} ${props.btnDisabled ? 'disabled' : ''}`;
    return (React.createElement("button", { className: classStr, onClick: props.onClick }, props.name.toUpperCase()));
};
export default GameButton;
